              {{ errors.first(`addEditValidation.name_ar`) }}
            </div>
          </v-col>

             <v-col class="py-0" :cols="6">
            <div
              :class="{
                'has-error': errors.has(`addEditValidation.name_en`),
              }"
            >
             <label>{{$t('NameEn')}}</label>
              <v-text-field
                class="d-block my-2"
